import React from 'react';
import { Box, Typography, Button, Card, CardMedia, CardContent, CardActions } from '@material-ui/core';
import LocationOnIcon from '@material-ui/icons/LocationOn';
import PhoneIcon from '@material-ui/icons/Phone';
import Rating from '@material-ui/lab/Rating';
import Collapse from '@material-ui/core/Collapse';
// eslint-disable-next-line
import clsx from 'clsx';
import IconButton from '@material-ui/core/IconButton';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';

import useStyles from './styles.js';

const placeHolderPhoto = 'https://www.foodserviceandhospitality.com/wp-content/uploads/2016/09/Restaurant-Placeholder-001.jpg';

const mkPhotoUrl = (place) => {
  const photoRef = place.photos && place.photos[0];
  if (!photoRef) {
    return placeHolderPhoto;
  }
  return `https://maps.googleapis.com/maps/api/place/photo?photo_reference=${photoRef}&key=AIzaSyCxfXAPgVM8ownCZdr8dFbQSg76chB7P4s&maxheight=350`;
};


const format_days = (start_day, end_day) =>
  start_day === end_day ? start_day : start_day + '-' + end_day;


const PlaceDetails = ({ place, selected, refProp }) => {
  if (selected) refProp?.current?.scrollIntoView({ behavior: 'smooth', block: 'start' });
  const classes = useStyles();
  const [expanded, setExpanded] = React.useState(false);

  const handleExpandClick = () => {
    setExpanded(!expanded);
  };

  return (
    <Card elevation={6}>
      <CardMedia
        style={{ height: 350 }}
        image={mkPhotoUrl(place)}
        title={place.name}
      />
      <CardContent>
        <Typography gutterBottom variant="h5">{place.place_name}</Typography>
        <Box display="flex" justifyContent="space-between" my={2}>
          <Rating name="read-only" value={Number(place.rating)} readOnly />
          <Typography component="legend">{place.user_ratings_total} review{place.user_ratings_total > 1 && 's'}</Typography>
        </Box>
        <Box display="flex" justifyContent="space-between">
          {place.price_level !== undefined
            && (
              <>
                <Typography component="legend">Price</Typography>
                <Typography gutterBottom variant="subtitle1">
                  {'$'.repeat(place.price_level + 1)}
                </Typography>
              </>
            )}
        </Box>
        {place.times.map((time) => (
          <Box display="flex" justifyContent="space-between" my={1} alignItems="center">
            <Typography variant="subtitle2" color="textSecondary">{format_days(time.start_day, time.end_day)}: {time.start_time}-{time.end_time}</Typography>
          </Box>
        ))}
        <hr></hr>
        {place.deals.slice(0, 5).map((deal) => (
          <Box display="flex" justifyContent="space-between" my={1} alignItems="center">
            <Typography variant="subtitle2" color="textSecondary">{deal.discount_amount}{deal.discount_marker}: {deal.item}</Typography>
          </Box>
        ))}
        {place.deals.length > 5
          && (
            <>
              <CardActions disableSpacing>
                <Typography variant="subtitle2" color="textSecondary">+ {place.deals.length - 5} more!</Typography>
                <IconButton
                  className={clsx(classes.expand, {
                    [classes.expandOpen]: expanded,
                  })}
                  onClick={handleExpandClick}
                  aria-expanded={expanded}
                  aria-label="show more"
                >
                  <ExpandMoreIcon />
                </IconButton>
              </CardActions>
              <Collapse in={expanded} timeout="auto" unmountOnExit>
                {place.deals.slice(5).map((deal) => (
                  <Box display="flex" justifyContent="space-between" my={1} alignItems="center">
                    <Typography variant="subtitle2" color="textSecondary">{deal.discount_amount}{deal.discount_marker}: {deal.item}</Typography>
                  </Box>
                ))}
              </Collapse>
            </>
          )}
        {place.formatted_address && (
          <Typography gutterBottom variant="body2" color="textSecondary" className={classes.subtitle}>
            <LocationOnIcon />
            <Button size="small" color="seconday" onClick={() => window.open(place.gmap_url, '_blank')}>
              {place.formatted_address}
            </Button>
          </Typography>
        )}
        {place.formatted_phone && (
          <Typography variant="body2" color="textSecondary" className={classes.spacing}>
            <PhoneIcon /> {place.formatted_phone}
          </Typography>
        )}
      </CardContent>
      <CardActions>
        <Button size="small" color="seconday" onClick={() => window.open(place.website, '_blank')}>
          Website
        </Button>
      </CardActions>
    </Card>
  );
};

export default PlaceDetails;
